.side-panel {
  position: fixed;
  width: 300px;
  height: 100%;
  top: 0;
  background-color: #f1f1f1;
  z-index: 10;
  display: block;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  font-size: inherit;
}
.side-panel.show {
  right: 0;
}
.side-panel.hidden {
  right: -300px;
}


/* FILTER BAR */

.side-panel-bar {
  -ms-flex: 1 1;
      flex: 1 1;
  width: 100%;
  height: 62px;
  border-bottom: 2px solid #999;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-pack: center;
      justify-content: center;

}
.side-panel-bar input {
  width: calc(100% - 60px);
  border: none;
  background: none;
  font-family: 'Roboto Mono', monospace;
  font-weight: 400;
  font-size: inherit;
}

/* LIST OF LOCATIONS */

.places-list {
  width: 100%;
  height: calc(100vh - 62px);
  padding: 1.5em;
  overflow-y: scroll;
  text-align: left;
}

/* HAMBURGER ---> './Hamburger.css' */

.side-panel .message {
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 0.1em;
  text-align: center;
  color: #999;
  padding: 0;
  text-transform: uppercase;
  margin: 0;
}


@media screen and (max-width: 360px) {
  .side-panel {
    width: 100%;
    height: calc(100vh - 62px);
    top: 62px;
  }
  .side-panel.show {
    right: 0;
  }
  .side-panel.hidden {
    right: -100%;
  }
  .places-list {
    height: calc(100% - 62px);
  }
  .hamburger {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 999;
  }
  main.panel-show,
  main.panel-hidden {
    width: 100%;
  }
}



