.marker {
  width: 48px;
  height: 64px;
  -webkit-transform: translate(-50%, -100%);
      -ms-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  cursor: pointer;
}

.marker.bounce {
  -webkit-animation: bounce 0.8s cubic-bezier(.20,.70,.80,.60);
          animation: bounce 0.8s cubic-bezier(.20,.70,.80,.60);
}


@-webkit-keyframes bounce {
   33% { -webkit-transform: translate(-50%, -100%); transform: translate(-50%, -100%); }
   66% { -webkit-transform: translate(-50%, -130%); transform: translate(-50%, -130%); }
  100% { -webkit-transform: translate(-50%, -100%); transform: translate(-50%, -100%); }
}


@keyframes bounce {
   33% { -webkit-transform: translate(-50%, -100%); transform: translate(-50%, -100%); }
   66% { -webkit-transform: translate(-50%, -130%); transform: translate(-50%, -130%); }
  100% { -webkit-transform: translate(-50%, -100%); transform: translate(-50%, -100%); }
}



