* {
  outline: none;
}
body {
  overflow: hidden;
}
header,
main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.app {
  text-align: center;
  position: relative;
}



/* HEADER COMPONENT */

.logo {
  height: 62px;
  width: 100%;
  background-color: #fff;
  border-bottom: 2px solid #999;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.flag {
  width: 50px;
  height: 36px;
  margin: auto 1em;
}
.slogan {
  margin: auto 1em auto 0;
  padding: 0;
  color: #06a;
  font-size: 1.2em;
  font-weight: 400;
}
@media screen and (max-width: 360px) {
  .slogan {
    display: none;
  }
}


/* MAP COMPONENT */
main {
  height: calc(100vh - 62px);
  -webkit-transition: width 0.3s;
  -o-transition: width 0.3s;
  transition: width 0.3s;
  outline: none;
  overflow: hidden;
}
main.panel-hidden {
  width: 100%;
}

main.panel-show {
  width: calc(100% - 300px);
}


/* COMMON ELEMENTS */

.details-button {
  border: none;
  outline: none;
  padding: 0.8em;
  font-family: inherit;
  font-size: 1rem;
  background-color: #06a;
  color: #fff;
  cursor: pointer;
}
.details-button:hover {
  background-color: #004d80;
}
.details-button:active {
  background-color: #06a;
}




/* ERROR message */

.error {
  position: absolute;
  top: 62px;
  left: 0;
  width: 100%;
  font-family: 'Roboto Mono', monospace;
  font-size: 0.8em;
  background-color: #fd0;
  text-align: left;
  padding: 1em;
}
.error span {
  text-decoration: underline;
  text-transform: uppercase;
  -webkit-text-decoration-color: #fff;
          text-decoration-color: #fff;
}
.error ol {
  margin: 0;
}
.error li {
  list-style: decimal;
}



